<template>
  <div style="display: flex; align-items: start; justify-content: center">
    <v-card
      flat
      tile
      width="95%"
      class="d-flex flex-row-reverse mt-4 px-8 py-4 text-center"
      style="border-radius: 25px 25px 0 0 !important"
    >
      <v-menu open-on-click style="border-radius: 10px">
        <template v-slot:activator="{ on }">
          <!-- <v-icon color="grey darken-2" class="mr-8">mdi-alert-circle-outline</v-icon> -->
          <v-btn x-large outlined style="border: 1px solid white">
            <v-avatar color="primary" size="48" v-on="on">
              <span class="white--text text-h5">{{ user_init }}</span>
            </v-avatar>
          </v-btn>
        </template>
        <v-list min-height="100" min-width="250" class="pa-3">
          <v-row class="px-1">
            <v-col cols="3" md="3" sm="6" class="text-right ma-auto pa-auto">
              <v-avatar color="primary" size="48">
                <span class="white--text text-h5">{{ user_init }}</span>
              </v-avatar>
            </v-col>
            <v-col>
              <h4 class="text-left">{{ user }}</h4>
              <h5 class="text-left" style="font-weight: 500">{{ nik }}</h5>
            </v-col>
          </v-row>
          <v-divider class="mt-3 mb-3"></v-divider>
          <v-row>
            <v-col class="mb-3 pb-1">
              <v-btn
                small
                right
                color="red darken-1"
                class="noTextTransform ml-auto"
                style="color: white; float: left"
                @click="onLogout"
              >
                Logout
              </v-btn>
            </v-col>
          </v-row>
        </v-list>
      </v-menu>
      <!-- <v-avatar class="mx-2" size="48">
        <v-icon color="grey darken-2">mdi-cog-outline</v-icon>
      </v-avatar>
      <v-avatar class="mx-2" size="48">
        <v-badge overlap color="red" content="6" class="my-auto"> 
          <v-icon color="grey darken-2">mdi-bell-outline</v-icon>
        </v-badge> 
      </v-avatar>
      <v-avatar class="mx-2" size="48">
        <v-icon color="grey darken-2">mdi-alert-circle-outline</v-icon>
      </v-avatar> -->
    </v-card>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  name: "Header",

  components: {},

  data() {
    return {
      user_init: null,
      // localStorage.user_fname[0].toUpperCase() +
      // localStorage.user_lname[0].toUpperCase(),
      user: null,
      // localStorage.user_fname[0].toUpperCase() +
      // localStorage.user_fname.slice(1),
      nik: null,
      // nik: localStorage.nik,
    };
  },

  created() {
    this.user_init =
      localStorage.user_fname[0].toUpperCase() +
      localStorage.user_lname[0].toUpperCase();
    this.user =
      localStorage.user_fname[0].toUpperCase() +
      localStorage.user_fname.slice(1);
    this.nik = localStorage.nik;
  },

  methods: {
    async onLogout() {
      const headers = {
        "Content-Type": "application/json",
        Host: "<calculated when request is sent>",
        Accept: "*/*",
        "Accept-Encoding": "gzip, deflate, br",
        Connection: "keep-alive",
        token: localStorage.token,
        username: localStorage.nik,
        app: "sso",
      };

      await axios
        .post("http://10.4.1.135:5001/sso/logout", headers)
        .then((response) => {
          // console.log(localStorage.token, localStorage.nik);
          // console.log(response.data.response_code);

          if (response.data.response_code == 200) {
            alert("Server error !");
          }
          localStorage.removeItem("user_fname");
          localStorage.removeItem("user_lname");
          localStorage.removeItem("token");
          localStorage.removeItem("nik");
          this.$router.push({ name: "login" });
        })
        .catch((error) => {
          console.log("Header.vue (error) :: ", error);
          alert("Server error!");
          localStorage.removeItem("user_fname");
          localStorage.removeItem("user_lname");
          localStorage.removeItem("token");
          localStorage.removeItem("nik");
          this.$router.push({ name: "login" });
        });
    },
  },
};
</script>

<style scoped>
</style>


