import Vuex from 'vuex';
import Vue from 'vue';

// Load Vuex
Vue.use(Vuex);

// Create store
export default new Vuex.Store({
  state: {
    u: null,  //user
  },

  mutations: {
    setUser(state, user) {
      state.u = user;
      },

    reset(state){
      state.u = "";
    }
  },

  actions: {},

  modules: {}
});
