import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter({
  // mode: 'history',
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [
    { path: "*", redirect: "/login" },
    {
      path: '/',
      name: 'login',
      component: () => import('../views/Login.vue'),
      meta: {
        hideNavbar: true,
      }
    },
    {
      path: '/tables',
      name: 'tables',
      component: () => import('../views/Tables.vue')
    },
    {
      path: '/input-form',
      name: 'input-form',
      component: () => import('../views/InputForm.vue')
    },
    {
      path: '/edit-form',
      name: 'edit-form',
      component: () => import('../views/EditForm.vue')
    },
    // {
    //   path: '/upload-voucher-code',
    //   name: 'upload-voucher-code',
    //   component: () => import('../views/UploadVoucherCode.vue')
    // }
  ]
})


export default router;